.table-title-item {
  display: flex;
  padding-top: 16px;
}
.table-title {
  font-weight: 700;
  font-size: large;
}

.table-container {
  margin-top: 16px;
  border: 1px solid var(--lightergray);
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.13);
}

.table-head-item {
  font-weight: 700;
}

.table-body {
  border-radius: 6px;
  border-color: var(--primary-color) !important;
}

.table-body > .MuiTableRow-root:nth-child(odd) {
  background-color: var(--lightergray);
}
