}

.filter-button {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  padding-top: 8px;
}

.filter-drop-select {
  width: 100%;
}

.filter-drop-form,
.filter-value-text {
  width: 100%;
}
